import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthManagerService } from '../auth-manager.service';
// import { NavbarService } from 'src/app/core/navbar/navbar.service';
import { Router, ActivatedRoute } from '@angular/router';
// import { UserService } from 'src/app/core/user.service';
import { IUser } from '../../core/user.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AuthUserService } from 'src/app/core/auth-user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit, OnDestroy {
  private ngUnsubcribe = new Subject();
  loginForm: FormGroup;
  logginIn = false;
  error: string;
  constructor(private router: Router, private formBuilder: FormBuilder,
    private authManagerService: AuthManagerService,
    private route: ActivatedRoute,
    private authUserService: AuthUserService
  ) {
  }
  
  ngOnInit(): void {
    this.loginForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.pattern(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)]],
      password: ['', Validators.required]
    });
  }

  ngOnDestroy() {
    this.ngUnsubcribe.next();
    this.ngUnsubcribe.complete();
  }

  login(): void {
    if (this.loginForm.valid) {
      this.logginIn = true;
      this.authManagerService.login(this.loginForm.get('email').value.trim(), this.loginForm.get('password').value.trim())
        .pipe(takeUntil(this.ngUnsubcribe))
        .subscribe({next:  (user: IUser) => {
          console.log('Logged User: ', user);
          this.authUserService.login(user);
          this.logginIn = false;
      
        }, 
        error:(error: any)=> {
          this.error = error['error']['error'];
          this.logginIn = false;
        }});
    }

  }
}
